.widget {
	font-size: pxrem(14);
	line-height: 1.4;

	& + & {
		margin-top: 3em;
	}

	select {
		max-width: 100%;
	}
}

.widget-title {
	margin-bottom: pxrem(16);
	font-size: pxrem(18);
	font-weight: 500;
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
}

.widget-title-secondary-action {
	font-size: pxrem(12);
	font-weight: 400;
	color: var(--color-text-light);

	&:hover,
	&:focus {
		color: var(--color-link);
	}
}

/**
 * Core widgets
 */
.widget_media_image,
.widget_custom_html {
	img {
		display: block;
		border-radius: 4px;
	}
}

.widget_calendar {
	.wp-calendar-table {
		margin-bottom: 8px;

		caption { /* Month & Year */
			font-size: 1.1em;
			padding: 0.4em;
			text-align: center;
		}

		th,
		td {
			text-align: center;
			padding: 0.3em;
		}
	}
}

.widget_recent_comments {
	.recentcommentstexttop,
	.recentcommentstextend {
		vertical-align: middle;
	}
}
