.p2020-unread-post,
.post.p2020-unread-mention {
	&::before {
		content: "New";
		color: var(--color-green);
		background: var(--color-green-light);
		position: absolute;
		top: 0;
		border-radius: 0 0 3px 3px;
		text-transform: uppercase;
		font-size: 0.625rem;
		font-weight: 600;
		left: 24px;
		letter-spacing: 1px;
		padding: 0.25rem 0.75rem;
		width: auto;
		height: auto;
	}
}

.p2020-unread-comment,
.comment.p2020-unread-mention {
	> .o2-comment-header > .comment-meta {
		&::after {
			content: "New";
			color: var(--color-white);
			background: var(--color-green);
			text-transform: uppercase;
			border-radius: 10px;
			font-size: pxrem(9);
			font-weight: 500;
			letter-spacing: 0.1em;
			margin-left: 5px;
			padding: pxrem(4) pxrem(8);
		}
	}
}
