.p2020-sidebar__mobile-menu-close {
	display: none;
	margin-bottom: 8px;

	@at-root .is-mobile-menu & {
		display: flex;
		justify-content: flex-end;
	}
}

.p2020-sidebar__mobile-menu-close-button {
	appearance: none;
	padding: 8px;
	border: 0;

	&::before {
		@include icon("custom-close", 20px, var(--color-white));
	}

	&:focus {
		outline: 1px dotted var(--color-white);
	}
}

.p2020-sidebar__header-image {
	display: block;
}

.p2020-sidebar__inner-main {
	padding: 40px;

	@at-root .is-mobile-menu & {
		background: var(--color-white);
	}

	ul {
		list-style-type: none;
		margin: 0;

		ul {
			margin-left: 1em;
		}
	}

	@include breakpoint( "tablet-portrait" ) {
		padding: 20px;
	}
}

.p2020-sidebar__search {
	display: none;

	@include breakpoint( "tablet-portrait" ) {
		display: block;
		margin-bottom: 28px;
	}
}

.p2020-sidebar__footer {
	margin-top: 4em;
	color: var(--color-text-light);
	font-size: pxrem(13);

	&::before {
		content: "";
		display: block;
		border-top: 1px solid var(--color-border);
		width: 32px;
		margin-bottom: 1.25em;
	}

	button { // reset
		margin: 0;
		padding: 0;
		border: 0;
		background: transparent;
		appearance: none;
		box-shadow: none;
		font-weight: inherit;
	}

	a,
	button {
		color: currentColor;

		&:hover {
			color: var(--color-link);
		}
	}
}

.p2020-sidebar__footer-links {
	li + li {
		margin-top: 0.1em;
	}
}

.p2020-sidebar__footer-credit {
	color: var(--color-text);
	margin-top: 2.5em;
	font-weight: 500;
}
