.navigation {
	overflow: hidden;
	display: grid;
	grid-template-columns: [older] 50% [newer] 50%;
	border-top: 1px solid var(--color-border);

	.nav-older,
	.nav-newer {
		margin: 0;
	}

	.nav-older {
		grid-column-start: older;

		&:first-child {
			border-right: 1px solid var(--color-border);
		}
	}

	.nav-newer {
		grid-column-start: newer;
		text-align: right;

		&:first-child {
			border-left: 1px solid var(--color-border);
		}
	}

	a {
		display: block;
		height: 100%;
		padding: 1.2em;
		color: var(--color-text-light);

		&:hover {
			color: var(--color-link);
		}
	}

	@include breakpoint("handset-landscape") {
		display: block;

		.nav-older,
		.nav-newer {
			&:first-child {
				border: 0;
			}
		}

		.nav-newer:not(:first-child) {
			border-top: 1px solid var(--color-border);
		}
	}
}
