%p2020-contributors {
	.p2020-contributors {
		clear: both;
		margin: 4em 0 3em;
	}

	.p2020-contributors__heading {
		position: relative;
		margin-bottom: 2.5em;
		font-size: pxrem(13);

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: -5%;
			bottom: 0;
			display: block;
			width: 110%;
			height: 1px;
			margin: auto 0;
			background: var(--color-border-light);

			@include breakpoint("handset-landscape") {
				left: 0;
				width: 100%;
			}
		}
	}

	.p2020-contributors__heading-text {
		$padding: 8px;

		position: relative;
		left: -#{$padding};
		z-index: 1;
		padding: 0 $padding;
		background: var(--color-white);
		font-weight: 600;
		color: var(--color-text-light);
		text-transform: uppercase;
	}

	.p2020-contributors__list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 20px;
		margin: 0;
		padding: 0;
		list-style: none;
		line-height: 1.3;

		@include breakpoint("handset-landscape") {
			grid-template-columns: repeat(2, 1fr);
		}

		@include breakpoint("handset-portrait") {
			grid-template-columns: 1fr;
		}
	}

	.p2020-contributors__item {
		display: flex;
		align-items: flex-start;
	}

	.p2020-contributors__item-avatar {
		flex: 0 0 auto;
		margin-right: 10px;
	}

	.p2020-contributors__item-name {
		font-size: pxrem(15);
		font-weight: 600;
		text-decoration: none;
		word-break: break-all;
	}

	.p2020-contributors__item-revisions {
		font-size: pxrem(12);
		color: var(--color-text-light);
	}
}
