.intralinks {
	width: 100%;
	margin-top: 1.5em !important;
	padding-top: 1.5em;
	border-top: 1px solid var(--color-border-light);
	border-bottom: 1px solid var(--color-border-light);
	color: var(--color-text-light);

	.intralinks-count {
		margin-bottom: 1.5em !important;
		color: var(--color-text);
	}

	.intralink-content-preview {
		position: absolute;
		top: 0;
		left: 128px;
		font-size: pxrem(11);
		line-height: 2;
		text-indent: -800%;

		&:hover {
			color: var(--color-link);
		}

		&:focus {
			outline: 0;
		}
	}

	a[href^="http"] {
		color: var(--color-text);
		text-decoration: none;

		&:hover {
			color: var(--color-link);
		}
	}
}

.intralink-to-thread {
	position: relative;
	white-space: nowrap;
	overflow-x: hidden;
	text-overflow: ellipsis;
}

.intralink-date {
	width: 10em;
}

.intralink-content {
	margin-top: 8px !important;
	margin-bottom: 4px !important;
	white-space: normal;
}
