.widget_office-hours-widget {
	.timezone-group {
		ul {
			margin-left: 0;
		}

		li { // User block
			position: relative;
			overflow: hidden;
			margin-top: 8px;
			padding: 10px 0 10px 50px;
		}
	}

	.avatar {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		margin: auto;
	}

	.widget_officehours_meta {
		margin-right: 57px;
		font-size: pxrem(12);
		line-height: 1.2;
		color: var(--color-text-light);
	}

	.widget_officehours_badge {
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		width: 4.1em;
		padding: 32px 4px 10px;
		font-size: pxrem(12);
		text-align: center;
		color: var(--color-text-light);
		border-radius: 6px;

		&.day {
			background: var(--color-white) url(data:image/svg+xml;base64,) no-repeat center 12px;
			background-size: 35%;
		}

		&.night {
			background: var(--color-background-light) url(data:image/svg+xml;base64,) no-repeat center 12px;
			background-size: 35%;
		}
	}
}
