/***************************************************************************************************
 * Master Threads as Sticky
 */
.blog .post.sticky.tag-master-thread {
	position: relative;
	margin-bottom: 2px;
	transition: all 200ms ease-in;
	border: none;
}

.blog .post.sticky.tag-master-thread + .post:not(.tag-master-thread) {
	margin-top: 40px;
}

.blog .post.sticky.tag-master-thread::after {
	content: "\f507";
	-webkit-font-smoothing: antialiased;
	font: 400 32px/1 Genericons, sans-serif;
	display: block;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 28px;
	padding: 3px;
	border-radius: 4px;
}

.blog .post.sticky.tag-master-thread::before {

	/* This is to not use the P2 "Status" styling which is meaningless in this context */
	display: none;
}

.blog .post.sticky.tag-master-thread h1::before {
	content: "Master Thread";
	display: block;
	text-transform: uppercase;
	font-size: pxrem(11);
	color: var(--color-text-light);
	transition: var(--transition-quick);
	letter-spacing: 0.01em;
	font-weight: 600;
	line-height: 1;
	margin-bottom: 2px;
}

.blog .post.sticky.tag-master-thread:hover h1::before {
	color: var(--color-text);
}

.blog .post.sticky.tag-master-thread .o2-post .entry-header {
	margin: 0;
}

.blog .post.sticky.tag-master-thread .o2-post {
	border: 0;
	padding: 20px 0 16px;
}

.blog .post.sticky.tag-master-thread h1 {
	margin-bottom: 0;
	margin-left: 0;
	padding-left: 10% !important;
	font-size: pxrem(22);
}

.blog .post.sticky.tag-master-thread .entry-content,
.blog .post.sticky.tag-master-thread .entry-meta,
.blog .post.sticky.tag-master-thread .author-avatar {
	display: none;
}


/***************************************************************************************************
 * Master Threads Shortcode
 */
.shortcode-master-thread {
	display: block;
	position: relative;
	background: #f3f6f8;
	border: 1px solid #d8e2e9;
	border-radius: 2px;
	box-shadow: inset 45px 0 0 0 #fff, inset 46px 0 0 0 #d8e2e9;
	padding: 8px 8px 8px 56px;
	margin: 4px 0;
	line-height: 1.2;
	transition: all 200ms ease-in-out;
}

.shortcode-master-thread:hover {
	border-color: #668eaa;
	box-shadow: inset 45px 0 0 0 #fff, inset 46px 0 0 0 #d8e2e9, 0 2px 2px 0 #d8e2e9;
}

.o2-posts a.shortcode-master-thread,
.o2-posts a.shortcode-master-thread:hover {

	/* These unfortunately needed higher specificity */
	text-decoration: none;
	color: #2e4453;
}

.shortcode-master-thread::before {
	content: "";
	display: block;
	position: absolute;
	top: calc(50% - 12px);
	left: 11px;
	background-image: url(data:image/svg+xml;base64,);
	width: 24px;
	height: 24px;
}

.shortcode-master-thread__subhead {
	font-size: 0.8em;
	text-transform: uppercase;
	color: #87a6bc;
}

.shortcode-master-thread__title {
	display: block;
	font-weight: 600;
}

.shortcode-master-thread__meta {
	font-size: 0.9em;
	color: #4f748e;
}
