body {
	overflow-x: hidden;
	display: flex;
	flex-direction: column;
	min-height: calc(100vh - #{$wpadminbar-height});

	@include breakpoint("wpadminbar-tablet") {
		min-height: auto;
	}
}

#page {
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
	flex: 1;
}

#masthead {
	position: sticky;
	top: 0; // for when there's no admin bar, like in the Customizer
	z-index: 1;
	width: 100%;

	@at-root .admin-bar & {
		top: $wpadminbar-height;

		@include breakpoint("wpadminbar-tablet") {
			top: $wpadminbar-height-mobile;
		}

		@include breakpoint("wpadminbar-handset") {
			position: static;
		}
	}
}

#main-wrapper {
	display: flex;
	flex-grow: 1;
}

#content {
	position: relative;
	z-index: 0;
	margin: 0 auto;
	width: 75%;
	max-width: 1020px; // should be in sync with $content_width in functions.php
	padding: 50px 40px;

	@include breakpoint("tablet-portrait") {
		width: 100%;
		padding: 16px;
	}
}

#sidebar {
	width: 25%;
	max-width: 360px;
	min-width: 280px;
	border-right: 1px solid var(--color-border);

	&.is-mobile-menu {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		width: 100%;
		margin: 12px auto;
		border: 0;
		box-shadow: $shadow-popover;
	}
}
