.p2020-site-header {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 0 16px;
	border-bottom: 1px solid var(--color-border);
	background: var(--color-white);
	min-height: 50px;

	@include breakpoint("tablet-portrait") {
		padding: 12px;
	}
}

.p2020-site-header__main {
	display: flex;
	align-items: center;
	min-width: 0;
	margin-right: 28px;
}

.p2020-site-header__search {
	position: relative;
	z-index: 1;
	background: var(--color-white);

	&::before {
		content: "";
		position: absolute;
		left: -28px;
		display: block;
		width: 28px;
		height: 100%;
		background: linear-gradient(90deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 1) 100%);
	}

	@include breakpoint("tablet-portrait") {
		display: none;
	}
}
